/* grid init */

.Grid {
  display: grid;
  height: 100%;
}

/* main */
.Grid.main {
  grid-template-rows: 40px 1fr;
}

.Grid.main > :nth-child(1) {
  background-color: #1775ee;
}

.Grid.main > :nth-child(2) {
  align-self: center;
}

/* sidebar */
.Grid.sidebar {
  grid-template-columns: minmax(200px, 1fr) 3fr;
  grid-gap: 20px;
}

.Grid.sidebar > :nth-child(1) {
  background-color: #BDBDBD;
}

.Line {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    padding: 0 20px;
    grid-gap: 20px;
    gap: 20px;
}

.Line.no-padding-left {
    padding-left: 0;
}


.Line[justify="center"] {
    justify-content: center;
}

.Line[justify="end"] {
    justify-content: flex-end;
}

.Line[justify="between"] {
    justify-content: space-between;
    padding: 0;
}

.Line[justify="around"] {
    justify-content: space-around;
}
